<template>
    <div class="day" @click="handlerClick" @click.shift.prevent.stop="$emit('debug', day)" :class="{'locked': day.locked}" @contextmenu.prevent="handlerRightClick">



        <span class="label">{{ day.label }}</span>

        <span class="cartouche wp xs" v-for="d in groupProject" :title="d.label" :class="{ 'conflict': d.status_id == 3 }">
            <i :class="'icon-status-' + d.status_id"></i>
            {{ d.acronym }}
            <span class="addon">
                {{d.duration | duration2(day.dayLength)}}
            </span>
        </span>

        <span class="cartouche teaching xs" v-if="day.teaching.length" title="Enseignement">
            Cours
            <span class="addon">
                {{totalEnseignement | duration2(day.dayLength)}}
            </span>
        </span>

        <span class="cartouche info xs" v-if="day.infos.length" title="Infos">
            Infos
            <span class="addon">
                {{totalInfo | duration2(day.dayLength)}}
            </span>
        </span>

        <span class="cartouche training xs" v-if="day.training.length" title="Formation">
            Formation
            <span class="addon">
                {{totalFormation | duration2(day.dayLength)}}
            </span>
        </span>

        <span class="cartouche vacations xs" v-if="day.conges.length" title="Congès">
            Congès
            <span class="addon">
                {{totalConges | duration2(day.dayLength)}}
            </span>
        </span>

        <span class="cartouche sickleave xs" v-if="day.sickleave.length" title="Arrêt maladie">
            A.Maladie
            <span class="addon">
               {{ totalSickleave | duration2(day.dayLength) }}
            </span>
        </span>

        <span class="cartouche sickleave xs" v-if="day.absent.length" title="Absence">
            Abs
            <span class="addon">
                 {{ totalAbsent | duration2(day.dayLength) }}
            </span>
        </span>

        <span class="cartouche research xs" v-if="day.research.length" title="Autre projet de recherche">
            Recherche
            <span class="addon">
                 {{ totalResearch | duration2(day.dayLength) }}
            </span>
        </span>

        <span v-if="day.closed" :title="day.lockedReason" style="font-size: .7em">
            <i class="icon-minus-circled"></i>
            Fermé
        </span>

        <span v-else-if="day.locked" :title="day.lockedReason" style="font-size: .7em">
            <i class="icon-lock"></i>
            Verrouillé
        </span>
        &nbsp;
    </div>
</template>

<style scoped lang="scss">
    .cartouche {
        white-space: nowrap;
        margin: 0;
        border-radius: 2px;
        box-shadow: none;
        font-size: .75em;
        &.training { background-color: #0b58a2; }
        &.sickleave { background-color: #808000; }
        &.wp { background-color: #6a5999; }
        &.conflict { background: #AA0000; }
    }

</style>

<script>

    export default {
        name: 'TimesheetMonthDay',

        props: {
            day: {
               require: true
            }
        },

        filters: {
            duration(v){
                let h = Math.floor(v);
                let m = Math.round((v - h)*60);
                if( m < 10 ) m = '0'+m;
                return h +':' +m;
            }
        },

        computed: {
            totalEnseignement(){
                let t = 0.0;
                this.day.teaching.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalInfo(){
                let t = 0.0;
                this.day.infos.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalFormation(){
                let t = 0.0;
                this.day.training.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalConges(){
                let t = 0.0;
                this.day.conges.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalSickleave(){
                let t = 0.0;
                this.day.sickleave.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalAbsent(){
                let t = 0.0;
                this.day.absent.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
            totalResearch(){
                let t = 0.0;
                this.day.research.forEach(d => {
                    t += d.duration;
                });
                return t;
            },
           groupProject(){
               let groups = {};
               this.day.declarations.forEach(d => {
                   if( !groups.hasOwnProperty(d.acronym) ){
                       groups[d.acronym] = {
                           label: d.label,
                           acronym: d.acronym,
                           duration: 0.0,
                           status_id: d.status_id
                       }
                   }
                   if( d.status_id != groups[d.acronym].status_id ){
                       groups[d.acronym].status_id = 0;
                   }
                   //groups[d.acronym].status_id += d.duration;
                   groups[d.acronym].duration += d.duration;
               });
               return groups;
           }
        },

        methods: {
            handlerClick(){
                this.$emit('selectDay', this.day);
            },

            handlerRightClick(e){
                this.$emit('daymenu', e, this.day);
            }
        }

    }
</script>


